import dva from 'dva';
import { Component } from 'react';
import createLoading from 'dva-loading';
import history from '@tmp/history';

let app = null;

export function _onCreate() {
  const plugins = require('umi/_runtimePlugin');
  const runtimeDva = plugins.mergeConfig('dva');
  app = dva({
    history,
    ...((require('D:/code/netlify/antDPro/src/dva.js').config || (() => ({})))()),
    ...(runtimeDva.config || {}),
    ...(window.g_useSSR ? { initialState: window.g_initialData } : {}),
  });
  
  app.use(createLoading());
  (runtimeDva.plugins || []).forEach(plugin => {
    app.use(plugin);
  });
  
  app.model({ namespace: 'global', ...(require('D:/code/netlify/antDPro/src/models/global.js').default) });
app.model({ namespace: 'login', ...(require('D:/code/netlify/antDPro/src/models/login.js').default) });
app.model({ namespace: 'music', ...(require('D:/code/netlify/antDPro/src/models/music.js').default) });
app.model({ namespace: 'register', ...(require('D:/code/netlify/antDPro/src/models/register.js').default) });
app.model({ namespace: 'setting', ...(require('D:/code/netlify/antDPro/src/models/setting.js').default) });
app.model({ namespace: 'user', ...(require('D:/code/netlify/antDPro/src/models/user.js').default) });
app.model({ namespace: 'userManage', ...(require('D:/code/netlify/antDPro/src/models/userManage.js').default) });
app.model({ namespace: 'userSetting', ...(require('D:/code/netlify/antDPro/src/models/userSetting.js').default) });
  return app;
}

export function getApp() {
  return app;
}

export class _DvaContainer extends Component {
  render() {
    const app = getApp();
    app.router(() => this.props.children);
    return app.start()();
  }
}
